'input', option);\n      selected.value = option;\n      open.value = false;\n      emit('input', selected.value);\n    }\n  }\n  else {\n      // emit('input', option.id);\n      selectedItems.value.push(option);\n      emit('input', selectedItems.value);\n      emit('afterClick');\n  }\n}\n\nconst getDisabledOptionsText = (index) => {\n  const opt = props.disabledOptions.find(e => e.index == index);\n  if(opt)\n    return opt.cause;\n  return undefined;\n}\nconst cleanSelected = (event) => {\n  event.stopPropagation();\n  selected.value = null;\n  emit('input', selected.value);\n}\nconst removeItem = (event, toRemove) => {\n  event.stopPropagation();\n  selectedItems.value = selectedItems.value.filter(item => item.ID != toRemove.ID);\n  emit('input', selectedItems.value);\n  emit('afterClick', true, toRemove);\n\n}\n</script>\n\n  \n  <style scoped>\n  /*You should put other CSS properties with same class names in the parent component's style tag that is not scoped.*/\n  * {\n    font-family: \"Roboto\"\n  }\n  .custom-select {\n    position: relative;\n    width: 100%;\n    text-align: left;\n    outline: none;\n  }\n  \n  .custom-select .selected {\n\n    cursor: pointer;\n    user-select: none;\n  }\n  \n  .custom-select .selected .select-down, \n  .custom-select .selected .cleanIcon {\n    position: absolute;\n    right: 10px;\n    float: right;\n  }\n  \n  .custom-select .items {\n    position: absolute;\n    left: 0;\n    right: 0;\n    z-index: 1;\n  }\n  \n  .custom-select .items .option {\n    cursor: pointer;\n    user-select: none;\n  }  \n  \n  .custom-select .items .option.disabled div {\n    cursor: default;\n    user-select: none;\n    opacity: 0.4;\n  } \n  .custom-select .items .option.disabled:active {\n    pointer-events: none;\n  }\n  \n  .selectHide {\n    display: none;\n  }\n  .button-div {\n    justify-content: center;\n    display: grid;\n    padding: 13px 0 19px;\n    margin-top: 10px\n  }\n  .button {\n    cursor: pointer;\n  }\n  .custo